<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process202"></a>

<p><br></p>
<p><span class="rvts17">损坏的雕像（</span><span class="rvts50">The Broken Statue</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:578,Y:157</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：天使雕像的头（</span><span class="rvts20">The Head of the Angel Statue</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">修复天使雕像，打开雕像后面的秘门</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1248.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在（位置</span><span class="rvts20">X:578,Y:157</span><span class="rvts21">）石墓花园的一处荒地上，有一个掉落的天使石雕头像。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1249.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">找到损害的天使雕像，尝试修复它，并且再次使两个天使雕像头朝外保持一致，可以解锁它们驻守的石墓，获得额外装备。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



